.Diary {
  background: white;
  min-width: 280px;
  max-width: 600px;
  border-radius: 5px;
  padding: 10px;
  margin: 20px auto;
  -webkit-box-shadow: 0px 0px 1px #999999;
          box-shadow: 0px 0px 1px #999999;
}

.DiaryTitle {
  text-align: center;
  font-size: 30px;
  border-bottom: 1px solid #E9E9E9;
  padding-bottom: 5px;
  margin-bottom: 5px;
  font-family: 'Mogra', cursive;
}

.DiaryImage {
  display: block;
  margin: 10px;
  width: calc(100% - 20px);
  border-radius: 5px;
}



